import React from 'react'
import { Table,Graph } from 'components';
import { Button,Modal, Input, Upload } from 'antd'
import { UploadOutlined } from '@ant-design/icons';
import './patienttable.scss'
import { patientApi } from 'utils/api'
import { LoadingOutlined } from '@ant-design/icons';
const PatientsTable = ({
  handleAdd,
  handleUpdate,
  handleDelete,
  graphData,
  data,
  columns,
  isVisiable,
  setIsVisiableGraph,
  isEditVisiable,
  editValue,
  handleChangeEditInput,
  handleChangeInput,
  setIsVisiable,
  setIsEditVisiable,
  setEditValue,
  inputValue,
  handleUpload,
  props,
  isVisiableGraph,
  isLoading
}) => {

  const mystyle = {
    fontSize: '60px',
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%,-50%)'
  };
    return (
      <>
      {isLoading && <LoadingOutlined style={mystyle}/>}
     {isVisiableGraph && <Graph graphData={graphData} isVisiableGraph={isVisiableGraph} setIsVisiableGraph={setIsVisiableGraph}/>}
      <div className="patients">
      <Button className="doctors__btn" onClick={() => setIsVisiable(!isVisiable)}></Button>
      <Modal
        title=" "
        visible={isVisiable}
        onOk={handleAdd}
        onCancel={() => setIsVisiable(false)}
      >
        <Input placeholder="" onChange={handleChangeInput} name="FirstName" value={inputValue.FirstName}/>
        <Input placeholder="" onChange={handleChangeInput} name="LastName" value={inputValue.LastName}/>
        <Input placeholder="" onChange={handleChangeInput} name="MiddleName" value={inputValue.MiddleName}/>
        <Input placeholder="" onChange={handleChangeInput} name="Age" value={inputValue.Age}/>
        <Input placeholder="" onChange={handleChangeInput} name="Weight" value={inputValue.Weight}/>
        <Input placeholder="" onChange={handleChangeInput} name="Height" value={inputValue.Height}/>
        <Input placeholder="" onChange={handleChangeInput} name="Sex" value={inputValue.Sex}/>
        <Input placeholder=" " onChange={handleChangeInput} name="PhoneNumber" value={inputValue.PhoneNumber}/>
        <Input placeholder=" " onChange={handleChangeInput} name="HomeNumber" value={inputValue.HomeNumber}/>
        <Input placeholder="E-mail" onChange={handleChangeInput} name="Email" value={inputValue.Email}/>
        <Input placeholder=" " onChange={handleChangeInput} name="Condition" value={inputValue.Condition}/>
      </Modal>
      {editValue && <Modal
        title=" "
        visible={isEditVisiable}
        onOk={handleUpdate}
        onCancel={() => setIsEditVisiable(false)}
      >
        <Input placeholder="" value={editValue.firstName} onChange={handleChangeEditInput} name="firstName"/>
        <Input placeholder="" value={editValue.lastName} onChange={handleChangeEditInput} name="lastName"/>
        <Input placeholder="" value={editValue.middleName} onChange={handleChangeEditInput} name="middleName"/>
        <Input placeholder="" value={editValue.age} onChange={handleChangeEditInput} name="age"/>
        <Input placeholder="" value={editValue.weight} onChange={handleChangeEditInput} name="weight"/>
        <Input placeholder="" value={editValue.height} onChange={handleChangeEditInput} name="height"/>
        <Input placeholder="" value={editValue.sex} onChange={handleChangeEditInput} name="sex"/>
        <Input placeholder=" " value={editValue.phoneNumber} onChange={handleChangeEditInput} name="phoneNumber"/>
        <Input placeholder=" " value={editValue.homeNumber} onChange={handleChangeEditInput} name="homeNumber"/>
        <Input placeholder="E-mail" value={editValue.email} onChange={handleChangeEditInput} name="email"/>
        <Input placeholder=" " value={editValue.condition} onChange={handleChangeEditInput} name="condition"/>
        <Upload {...props} >
          <Button>
            <UploadOutlined />  
          </Button>
        </Upload>
        <Input type="file" name="file" onChange={handleUpload}/>
        <Button onClick={handleDelete}> </Button>
      </Modal>}
      <Table columns={columns} data={data} isEditVisiable={isEditVisiable} setIsEditVisiable={setIsEditVisiable} setEditValue={setEditValue} />     
      </div>
      </>
    )
}

export default PatientsTable